<template>
    <div class="ticket_containner">
        <div class="ticket_visit">
            <h1 class="ticket_text"></h1>
        </div>

        <div class="title_line"></div>

        
        <div class="ticket_content">
            <span>
                <p>Online pre-booking is recommended to get your timed ticket.</p>
                <p>-To maintain appropriate distance in the aquarium</p>
                <p>-To avoid waiting in a long line for getting tickets</p>
                <br>
                <br>
                <h2></h2>
                <br>
                <p></p>
                <p></p>
                <img src="~@/assets/price_230112.png" style="margin-top: 50px;" class="ticket_money">
                <p class="p_line">!! </p>
                <br>
                <br>
                <p class="small_p">1</p>
                <p class="small_p">JCBVISAAmerican ExpressMasterCardDiners</p>
                <img src="~@/assets/card.png">
                <p class="small_p"></p>
                <p class="small_p">ATM</p>
                <br>
                <br>
                <br>
                <p style="color:yellow">1520</p>
                <p>: 2,160</p>
                <p>: 1,120</p>
                <p>: 800</p>
                <p>: 500</p>
                <p></p>
            </span>
        </div>

        <img src="~@/assets/back_img01.png" class="back_img" :style="back_img_vue">

    </div>
</template>

<script>
export default {
    name:"BuyTicket",
    data() {
        return {
            back_img_vue:{},
            move_number:0
        }
    },
    mounted(){
        this.startAutoMove();
    },
    beforeDestroy(){
        this.startAutoMove();
    },
    methods:{
        move_backfish(){
            if(this.move_number == 0){           
                this.$set(this.back_img_vue, 'top', '-500px');
                this.move_number++;
                console.log("TOP1");
            }
            else if(this.move_number == 1){
                this.$set(this.back_img_vue, 'left', '300px');
                this.move_number++;
                console.log("LEFT2");
            }
            else if(this.move_number == 2){
                this.$set(this.back_img_vue, 'top', '-90px');
                this.move_number++;
                console.log("TOP3");
            }
            else if(this.move_number == 3){
                this.$set(this.back_img_vue, 'left', '0px');
                this.move_number = 0;
                console.log("left4");
            }
        },
        startAutoMove(){
            setInterval(() => {
                this.move_backfish();              
            }, 4000);
        }
    }
}
</script>

<style scoped>
    .ticket_visit{
        width: 100%;
        height: 150px;
        background-image: url("~@/assets/title_visit.png") ;
        background-size: cover;
        background-position: center;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .ticket_text{
        color:white;
    }

    .title_line{
        background-image: url("~@/assets/title_line.png") ;
        width: 100%;
        height: 4px;
    }
    .ticket_content{
        width: 100%;
        background: cornflowerblue;
        height: 950px;
        padding-top: 50px;
    }
    .ticket_content h2::after{
        content:"";
        display: block;
        width: 1000px;
        height: 0.5px;
        background-color: white;
        margin:auto;
    }
    .p_line::after{
        content:"";
        display: block;
        width: 900px;
        height: 0.5px;
        background-color: white;
        margin:auto;
    }
    h2{
        color: white;
    }
    p{
        color: white;
    }
    .small_p{
        font-size: 12px;
    }

    .ticket_containner{
        position: relative;
    }
    .back_img{
        position: absolute;
        width: 80%;
        height: 1200px;
        left: 0px;
        top: -90px;
        transition: all 1s;
    }
    @media screen and (max-width: 768px){
        .back_img{
            display: none;
