    <div class="studyactivity_containner">
        <div class="studyactivity_visit">
            <h1 class="studyactivity_text"></h1>
        </div>

        <div class="title_line"></div>
            
        <div class="stydyactivity_content">
            <h2> </h2>
        
            <h4>Osaka Aquarium Biological Research Institute of Iburi Center (OBIC)</h4>

            <div class="p_groups">
                <p>Iburi</p>
                <p></p>
            </div>

            <div class="p_groups">
                <p>1997924 </p>
                <p>2051,600</p>
            </div>

            <div class="p_groups">
                <p></p>
                <p></p>
            </div>

            <div class="p_groups">
                <p></p>
                <p>2001420</p>
            </div>

            <div class="p_groups">
                <p>2120091212193158853,300</p>
                <p></p>
            </div>

            <img src="~@/assets/study_ing.png">
        </div>
        <img src="~@/assets/back_img01.png" class="back_img" :style="back_img_vue">
    </div>
</template>

<script>
export default {
    name:"StudyActivity",
    data() {
        return {
            back_img_vue:{},
            move_number:0
        }
    },
    mounted(){
        this.startAutoMove();
    },
    beforeDestroy(){
        this.startAutoMove();
    },
    methods:{
        move_backfish(){
            
            if(this.move_number == 0){           
                this.$set(this.back_img_vue, 'top', '-500px');
                this.move_number++;
                console.log("TOP1");
            }
            else if(this.move_number == 1){
                this.$set(this.back_img_vue, 'left', '300px');
                this.move_number++;
                console.log("LEFT2");
            }
            else if(this.move_number == 2){
                this.$set(this.back_img_vue, 'top', '-90px');
                this.move_number++;
                console.log("TOP3");
            }
            else if(this.move_number == 3){
                this.$set(this.back_img_vue, 'left', '0px');
                this.move_number = 0;
                console.log("left4");
            }
        },
        startAutoMove(){
            setInterval(() => {
                this.move_backfish();              
            }, 4000);
        }
    }
}
</script>

